canvas{
    width: 100%;
    height: 100%;
    position: absolute;
    // z-index: 2;
}
.High2{
    position: relative; 
    height: 2000px;  
    background: url('../images/back.png') no-repeat;
    background-size: 100% 100%;
    background-position: center;
    @media (max-width:1500px){
        height: 1800px;  
    }
    @media (max-width:1000px){
        height: 1600px;  
    }
    @media (max-width:850px){
        height: 1400px;  
    }
    @media (max-width:767px){
        // background: url('../images/back1.jpg') no-repeat;
        background-size: 140% 100%;
        // background-position: top;
        height: 1300px;          
    }
    @media (max-width:590px){
        height: 1050px;          
    }
    @media (max-width:470px){
        height: 870px;          
    }
    @media (max-width:390px){
        height: 770px;          
    }
    @media (max-width:350px){
        height: 700px;          
    }
    .wrapper{
        width: 100%;
        height: 50%;
        position: absolute; 
        z-index: 1;
        left: 0;
        padding: 200px 15vw;
        display: flex;
        align-items: center;
        justify-content: start;
        // border: solid 1px gold;
        @media (min-width:767.1px){
            top: 25%;
        }
        @media (max-width:1629px){
            padding: 200px 15vw;
        }
        @media (max-width:1555px){
            padding:200px 15vw;
        }
        @media (max-width:1250px){
            padding: 200px 10vw;
        }
        @media (max-width:1000px){
            padding: 200px 5vw;
        }
        @media (max-width:870px){
            padding: 200px 10vw;
        }
        @media (max-width:767px){
            // border: solid 1px white;
            padding: 200px 10vw 50px 10vw;
            background-color: #021A38;
            // margin-top: 50%;
            height: 50%;
            bottom: 0;
        } 
        @media (max-width:590px){
            padding: 200px 10vw 100px 10vw;
        } 
        @media (max-width:470px){
            padding: 200px 10vw 200px 10vw;
        } 
        @media (max-width:390px){
            padding: 200px 10vw 230px 10vw;
        }       
        h1{
            text-align: left;
            color: white;
            font-weight: 700;
            font-size: 72px;
            @media (max-width:1629px){
                font-size: 62px;
            }
            @media (max-width:1150px){
                font-size: 50px;
            }
            @media (max-width:1000px){
                font-size: 45px;
            }
            @media (max-width:850px){
                font-size: 40px;
            }
            @media (max-width:700px){
                font-size: 30px;
            }
        }
        p{
            text-align: left;
            color: white;
            font-size: 18px;
            opacity: 0.7;
            @media (max-width:1629px){
                font-size: 16px;
            }
            @media (max-width:1150px){
                font-size: 14px;
            }
            @media (max-width:850px){
                font-size: 13px;
            }
            @media (max-width:700px){
                font-size: 12px;
            }
            @media (max-width:650px){
                font-size: 11px;
            }
            @media (max-width:400px){
                font-size: 10px;
            }
        }
        .btns{
            margin-top: 40px;
            .btn{
                border: solid 1px red;
                color: white;
                background-color: red;
                padding: 10px 20px;
                border-radius: 8px;
                &:hover{
                    background-color: white;
                    color: red;
                }
                @media (max-width:470px){
                    font-size: 14px;
                    padding: 10px;
                }
                @media (max-width:390px){
                    font-size: 12px;
                    padding: 10px;
                }
            }
            .media{
                background-color: white;
                margin-left: 30px;
                color: red;
                &:hover{
                    background-color:red;
                    color: white;
                }
                @media (max-width:390px){
                    margin-left: 10px;
                }
            }
        }
    }
    .social{
        position: absolute;
        width: 100%;
        // padding-top: 300px;
        // background-color: gold;
        left: 0;
        z-index: 2;
        bottom: 25%;
        display: flex;
        align-items: center;
        justify-content: center;
        transform-origin: center;
        transform: rotate(-30deg);
        @media (max-width:1600px){
            transform: rotate(-33deg);
        }
        @media (max-width:1430px){
            transform: rotate(-35deg);
        }
        @media (max-width:1300px){
            transform: rotate(-37deg);
        }
        @media (max-width:1200px){
            transform: rotate(-40deg);
        }
        @media (max-width:1070px){
            transform: rotate(-43deg);
        }
        @media (max-width:767px){
            transform: rotate(0deg);
            padding-top: 50px;
            bottom: 0%;
        }
        a{
            text-decoration: none;
            i{
                background-color: white;
                border-radius: 50%;
                padding: 0;
                cursor: pointer;
                margin: 0 50px;
                color: red;
                font-size: 77px;
                transform: rotate(30deg);
                @media (max-width:1430px){
                    transform: rotate(35deg);
                }
                @media (max-width:1300px){
                    transform: rotate(37deg);
                }
                @media (max-width:1200px){
                    transform: rotate(40deg);
                }
                @media (max-width:1070px){
                    transform: rotate(43deg);
                }
                @media (max-width:767px){
                    transform: rotate(0deg);
                    font-size: 60px;
                }
                @media (max-width:470px){
                    font-size: 50px;
                    margin: 0 30px;
                } 
            }
            .fa-instagram{
                border-radius: 20px;
                height: 65px;
                display: flex;
                align-items: center;
                @media (max-width:767px){
                    height: 50px;
                }
                @media (max-width:470px){
                    font-size: 50px;
                    height: 42px;
                } 
            }
            .fa-facebook-square{
                border-radius: 20px;
                height: 65px;
                display: flex;
                align-items: center;
                @media (max-width:767px){
                    height: 50px;
                }
                @media (max-width:470px){
                    font-size: 50px;
                    height: 42px;
                    border-radius: 15px;
                } 
            }
        }
    }
}