.Navbar{
    padding: 10px 100px;
    margin: 0;
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 10001;
    @media (max-width:1555px){
        padding: 10px 50px;
    }
    @media (max-width:1430px){
        padding: 10px 40px;
    }
    @media (max-width:1410px){
        padding: 10px 30px;
    }
    @media (max-width:1290px){
        padding: 10px 20px;
    }
    @media (max-width:845px){
        padding: 10px 10px;
    } 
    @media (max-width:855px){
        padding: 10px;
    } 
    @media (max-width:780px){
        padding: 10px 100px;
    } 
    @media (max-width:550px){
        padding: 10px 50px;
    }
    @media (max-width:330px){
        padding: 10px 30px;
    }        
    .w-750{
        width: 100%;
        // border: solid 1px; 
        @media (max-width:780px){
            min-width: 200px;
        }
        @media (max-width:450px){
            min-width: 150px;
        }
        @media (max-width:370px){
            min-width: 130px;
        }       
    }
    .white-img{
        @media (min-width:767.1px){
            display: none;
        } 
    }
    .black-img{
        @media (max-width:767px){
            display: none;
        } 
    }
    .tel{
        a{
            font-size: 16px;
            font-weight: 600;
            color: white;
            padding: 7px 20px;
            transition: 0.3s;
            border-radius: 7px;
            -webkit-border-radius: 7px;
            -moz-border-radius: 7px;
            -ms-border-radius: 7px;
            -o-border-radius: 7px;                
            @media (max-width:1215px){
                padding: 7px 10px;
            }
            @media (max-width:1095px){
                font-size: 14px;
            } 
            @media (max-width:910px){
                font-size: 12px;
            }    
            @media (max-width:780px){
               display: none;
            }       
        }
    }
    .navbar-nav{        
        @media (max-width:780px){
            z-index: 20005;
            display: block;
            position: absolute;
            top: -10px;
            right: -120vw;
            height: 120vh;
            width: 100vw;
            background-color: black;
            text-align: center;
            padding: 60px 0;
            transition: 0.4s;
            -webkit-transition: 0.4s;
            -moz-transition: 0.4s;
            -ms-transition: 0.4s;
            -o-transition: 0.4s;
        }             
        .nav-item{
            padding: 0 12px;
            font-weight: 500;
            @media (max-width:1410px){
                padding: 0 5px;
            }
            @media (max-width:1290px){
                padding: 0;
            } 
            @media (max-width:780px){
                padding: 20px 40px;
            }            
            .nav-link{
                color: white;
                padding: 7px 20px;
                transition: 0.3s;
                border-radius: 7px;
                font-weight: 400;
                -webkit-border-radius: 7px;
                -moz-border-radius: 7px;
                -ms-border-radius: 7px;
                -o-border-radius: 7px;                
                &:hover{
                    background-color: white;
                    color: black;
                    transform: scale(1.05);
                }
                &:active{
                    transform: scale(0.95);
                }
                @media (max-width:1215px){
                    padding: 7px 10px;
                }
                @media (max-width:1095px){
                    font-size: 14px;
                } 
                @media (max-width:910px){
                    font-size: 12px;
                }    
                @media (max-width:780px){
                    font-size: 16px;
                    color: white;
                }                          
            }
        }
        .tel-nav{
            @media (min-width:780.1px){
                display: none;
            } 
            .nav-link{
                &:hover{
                    background-color: black;
                    color: white;
                    transform: scale(1);
                }
                &:active{
                    transform: scale(1);
                }
            }
        }
    }    
    .show{
        right: -100px;  
        @media (max-width:550px){
            right: -50px;
        } 
        @media (max-width:330px){
            right: -30px;
        } 
    }
    .navbar-black{
        .navbar-nav{
            .nav-item{
                .nav-link{
                    color:#1F5497;
                    &:hover{
                        background-color: black;
                        color: white;                        
                    }
                    @media (max-width:767px){
                        color: white;
                        &:hover{
                            background-color: white;
                            color: black;                        
                        }
                    } 
                }
            }
        }
        .tel{
            a{
                color: #1F5497;
            }
        }
    }    
    .language{        
        .change-language{
            position: relative;     
            padding-right: 20px;           
            #language[type="checkbox"]{
                display: none;
            }
            label{
                color: white;
                cursor: pointer;
                font-weight: 600;
                font-size: 16px;
                padding: 5px 10px;
                transition: 0.3s;
                border-radius: 7px;
                -webkit-border-radius: 7px;
                -moz-border-radius: 7px;
                -ms-border-radius: 7px;
                -o-border-radius: 7px; 
                &:hover{
                    background-color: white;
                    color: black;
                    transform: scale(1.05);
                }
                &:active{
                    transform: scale(0.95);
                }
                @media (max-width:1215px){
                    padding: 7px 10px;
                }
                @media (max-width:1095px){
                    font-size: 14px;
                } 
                @media (max-width:910px){
                    font-size: 12px;
                }    
                @media (max-width:780px){
                    font-size: 16px;
                    color: white;
                } 
                @media (max-width:370px){
                    font-size: 14px;
                    color: white;
                } 
            }            
            .language-menu{
                position: absolute;
                text-align: center;
                margin-top: 10px;
                left: -15px;
                padding: 0;
                background-color: rgba(0, 0, 0, 0.829);    
                border-radius: 5px;                          
                li{
                    list-style: none;
                    padding: 0 10px;
                    transition: 0.4s;
                    -webkit-transition: 0.4s;
                    -moz-transition: 0.4s;
                    -ms-transition: 0.4s;
                    -o-transition: 0.4s;
                    &:hover{
                        background-color: white;
                        .nav-link{
                            color: black;
                        }
                    } 
                }
                a{
                    text-decoration: none;                   
                    .nav-link{
                        color: white;
                    }
                }
            }
        }        
    }
    .language.lang-black{
        label{
            color: #1F5497;
            &:hover{
                background-color: black;
                color: white;
                transform: scale(1.05);
            }
            @media (max-width:767px){
                color: white;
                &:hover{
                    background-color: white;
                    color: black;                        
                }
            } 
        }
    }
    .fa-bars{        
        font-size: 23px;
        cursor: pointer;
        color: white;
        @media (min-width:780.1px){
            display: none;               
        }
        @media (max-width:370px){
            font-size: 18px;
            color: white;
        }
    }
    .fa-times{
        position: absolute;
        font-size: 25px;
        top: 30px;
        right: 30px;
        cursor: pointer;
        color: white;
        @media (min-width:780.1px){
            display: none;               
        }
    }
}

.black{
    background-color: rgba(0, 0, 0, 0.753);
    transition-duration: 0.5s;    
}
.white{
    background-color: rgba(255, 255, 255, 0.89);
    transition-duration: 0.5s;  
    @media (max-width:767px){
        background-color: rgba(0, 0, 0, 0.753);
    }   
}

a{
    cursor: pointer;
}