.Contacts{
    padding: 0;
    margin: 0;
    background: url('../images/contact.png') no-repeat;
    background-size: 120% 100%;
    background-position-x: center;
    // display: flex;
    // align-items: center;
    // justify-content: center;
    .wrapper{
        padding: 150px 15vw;
        // display: flex;
        // align-items: center;
        // justify-content: center;
        @media (max-width:1300px){
            padding: 150px 30px;
        }     
        @media (max-width:767px){
            padding: 100px 30px;
        }
        @media (max-width:450px){
            padding: 100px 10px;
        }
        @media (max-width:400px){
            padding: 100px 0px;
        }   
        .row{     
            .title{
                padding: 20px;
                h1{
                    font-family: 'High Speed';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 40px;
                    color: white;
                }
                p{
                    color: white;
                    font-size: 20px;
                }
                @media (max-width:767px){
                    padding: 20px 30px;
                }
            }  
            .message{
                padding: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                .col-5{
                    padding: 0 10px;
                    margin-top: -25px;
                    // border: solid 1px;
                    .form-control{
                        margin-top: -40px;
                        background: #021a385d;
                        border: 0;
                        border-radius: 0;
                        padding: 40px 10px 12px 10px;
                        color: white;
                        &::placeholder{
                            color: white;
                        }
                        @media (max-width:550px){
                            font-size: 12px;
                            padding: 30px 10px 12px 10px;
                            margin-top: -30px;
                        }
                    }
                    .red-line{                   
                        border: solid 1px red;
                    }
                    .label1{
                        margin-top: 0;
                        margin-left: 10px;
                        color: #FFFFFF;
                        opacity: 0.35;
                        @media (max-width:550px){
                            font-size: 12px;
                        }
                    }
                    .label2{
                        margin-top: 40px;
                        margin-left: 10px;
                        color: #FFFFFF;
                        opacity: 0.35;     
                        @media (max-width:550px){
                            font-size: 12px;
                            margin-top: 20px;
                        } 
                    }
                    @media (max-width:767px){
                        width: 50%;                        
                    }
                    @media (max-width:550px){                       
                        margin-top: -10px;
                    } 
                    @media (max-width:450px){
                        padding: 0 5px;
                    }                   
                }
                .col-7{
                    position: relative;
                    padding: 0 10px;
                    .form-control{
                        background: #021a385d;
                        border: 0;
                        border-radius: 0;
                        padding: 40px 20px 0px 20px;
                        margin-top: -40px;
                        // margin-bottom: -20px;
                        color: white;
                        resize: none;
                        &::placeholder{
                            color: white;
                        }
                        @media (max-width:550px){
                            font-size: 12px;
                            padding: 30px 20px 0px 20px;
                            margin-top: -30px;
                        }
                    }
                    .red-line{                   
                        border: solid 1px red;
                    }
                    label{
                        margin-left: 20px;
                        color: #FFFFFF;
                        opacity: 0.35;
                        @media (max-width:550px){
                            font-size: 12px;
                        }
                    }
                    .col-3{
                        // border: solid 1px;
                        // position: absolute;
                        margin-top: -20px;
                        margin-left: 75%;
                        .btn{
                            background-color: red;
                            width: 100%;
                            color: white;
                            padding: 10px 0;
                            border-radius: 8px; 
                            @media (max-width:550px){
                                padding: 5px 0;
                                font-size: 12px;
                            }                         
                        } 
                        @media (max-width:550px){
                            width: 45%;
                            margin-left: 55%;
                        }                        
                    }
                    @media (max-width:767px){
                        width: 50%;                        
                    }
                    @media (max-width:450px){
                        padding: 0 5px;
                    }
                }
            }     
        }
    }
}