canvas{
    width: 120%;
    height: 100%;
    position: absolute;
    z-index: 1;
}
.onlyCanvas{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: black;
    z-index: 10001;
}
.car{
    @media (max-width:767px){
        height: 500px;
    }
    @media (max-width:600px){
        height: 400px;
    }
}
.High{
    background: url('../images/back.png') no-repeat;
    background-size: 100% 120%;
    background-position: center;
    position: relative;
    width: 100%;
    height: 1150px;
    @media (max-width:1500px){
        height: 1000px;  
    }
    @media (max-width:1000px){
        height: 850px;  
    }
    @media (max-width:850px){
        height: 700px;  
    }
    @media (max-width:590px){
        height: 550px;  
    }
    @media (max-width:470px){
        height: 450px;          
    }
    @media (max-width:390px){
        height: 400px;          
    }
    @media (max-width:350px){
        height: 370px;          
    }
    .text-bottom{
        position: absolute;
        bottom: 50px;
        left: 100px;
        h1{
            color: black;
        }
    }
    .title{
        position: absolute;
        top: 0;
        left: 0;
        padding: 100px 15vw;
        @media (max-width:1629px){
            padding: 100px 15vw;
        }
        @media (max-width:1555px){
            padding:100px 15vw;
        }
        @media (max-width:1250px){
            padding: 80px 10vw;
        }
        @media (max-width:1000px){
            padding: 5vw 15vw 80px 5vw;
        }        
        @media (max-width:767px){
            padding: 5vw 35vw 80px 5vw;
        }
        h1{
            text-align: left;
            color: black;
            font-weight: 700;
            font-size: 72px;
            line-height: 94%;
            @media (max-width:1629px){
                font-size: 62px;
            }
            @media (max-width:1341px){
                font-size: 55px;
            }
            @media (max-width:1150px){
                font-size: 50px;
            }
            @media (max-width:1000px){
                font-size: 45px;
            }
            @media (max-width:850px){
                font-size: 40px;
            }
            @media (max-width:700px){
                font-size: 30px;
            }
            @media (max-width:600px){
                font-size: 20px;
            }
            @media (max-width:400px){
                font-size: 18px;
            }
        }
        p{
            text-align: left;
            font-size: 18px;
            columns:#021A38;
            @media (max-width:1629px){
                font-size: 16px;
            }
            @media (max-width:1150px){
                font-size: 14px;
            }
            @media (max-width:850px){
                font-size: 13px;
            }
            @media (max-width:700px){
                font-size: 12px;
            }
            @media (max-width:600px){
                font-size: 11px;
            }
            @media (max-width:400px){
                font-size: 10px;
            }
        }
    }
    .wrapper{
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 0; 
        top: 0;
        left: 0;
        padding: 200px 15vw;
        display: flex;
        align-items: center;
        justify-content: end;
        @media (max-width:1629px){
            padding: 200px 15vw;
        }
        @media (max-width:1555px){
            padding:200px 15vw;
        }
        @media (max-width:1250px){
            padding: 200px 10vw;
        }
        @media (max-width:1000px){
            padding: 200px 5vw;
        }
        @media (max-width:870px){
            padding: 200px 5vw;
        }
        @media (max-width:767px){
            padding: 200px 5vw 200px 65vw;
        } 
        @media (max-width:600px){
            padding: 200px 3vw 200px 65vw;
        }       
        h1{
            text-align: left;
            color: white;
            font-weight: 700;
            font-size: 72px;
            @media (max-width:1629px){
                font-size: 62px;
            }
            @media (max-width:1150px){
                font-size: 50px;
            }
            @media (max-width:1000px){
                font-size: 45px;
            }
            @media (max-width:850px){
                font-size: 40px;
            }
            @media (max-width:700px){
                font-size: 30px;
            }
            @media (max-width:650px){
                font-size: 20px;
            }
            @media (max-width:400px){
                font-size: 18px;
            }
        }
        p{
            text-align: left;
            color: white;
            font-size: 18px;
            opacity: 0.7;
            @media (max-width:1629px){
                font-size: 16px;
            }
            @media (max-width:1150px){
                font-size: 14px;
            }
            @media (max-width:850px){
                font-size: 13px;
            }
            @media (max-width:700px){
                font-size: 12px;
            }
            @media (max-width:650px){
                font-size: 11px;
            }
            @media (max-width:400px){
                font-size: 10px;
            }
        }
    }
}