.canvas{
    width: 100%;
    height: 950px;
    // position: absolute;
    background-color: black;
    // z-index: 100001;
}
.Air{
    .contr-air{
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.377);
        z-index: 100001;
    }
    .air-modal{
        position: fixed;
        background-color: white;
        z-index: 100003;
        height: 70%;
        width: 60%;
        top: 15%;
        left: 20%;
        border-radius: 20px;
        overflow: auto;
        padding: 40px;
        @media (max-width:1300px) {
            width: 70%;
            left: 15%;
        }
        @media (max-width:900px) {
            width: 80%;
            left: 10%;
            border-radius: 15px;
        }
        @media (max-width:600px) {
            padding: 30px;
            border-radius: 10px;
        }
        @media (max-width:400px) {
            padding: 30px 20px;
            border-radius: 7px;
        }
        .fa-times{
            position: absolute;
            top: 15px;
            right: 20px;
            font-size: 25px;
            cursor: pointer;
            @media (max-width:600px) {
                top: 10px;
                right: 10px;
                font-size: 20px;
            }
        }
    }
}