.Numbers{
    position: relative;
    padding: 0;
    margin: 0;
    height: 2300px;
    width: 100%;
    overflow: hidden;
    @media (min-width:767.1px){
        background: url('../images/back.png') no-repeat;
        background-size: 100% 100%;
    }
    @media (max-width:767px){
        height: 120%;
        background-color: #021A38;
    }
    @media (max-width:500px){
        height: 110%;
    }
    @media (max-width:450px){
        height: 100%;
    }
    .wrapper{
        padding: 0 13vw 200px 13vw;
        @media (max-width:1400px){
            padding: 0 12vw 200px 10vw;
        }
        @media (max-width:1000px){
            padding: 0 12vw 200px 5vw;
        }
        @media (max-width:767px){
            padding: 130px 150px;
        }
        @media (max-width:700px){
            padding: 130px 100px;
        }
        @media (max-width:550px){
            padding: 130px 50px;
        }
        @media (max-width:320px){
            padding: 130px 30px;
        }
        .title{
            text-align: center;
            color: #021A38;
            font-weight: 700;
            font-size: 70.1213px;
            @media (max-width:1200px){
                font-size: 60px;
            }
            @media (max-width:850px){
                font-size: 50px;
            }
            @media (max-width:767px){
                display: none;
            }
        }
        .f1{
            margin-top: 50px;
            justify-content: center;
            .col-md-3{
                padding: 10px;                
                p{
                    font-style: normal;
                    font-weight: 700;
                    font-size: 16px;
                    color: #021A38;
                    line-height: 24px;
                    @media (max-width:1050px){
                        font-size:14px;
                        font-weight: 600;
                    }
                }
                span{
                    font-style: normal;
                    font-weight: 1000;
                    font-size: 80px;
                    line-height: 94%;
                    font-weight: 700px;
                    color: #021A38;
                    @media (max-width:1050px){
                        font-size: 60px;
                    }
                    @media (max-width:800px){
                        font-size: 50px;
                    }
                }
            }
            @media (max-width:767px){
                display: none;
            }
        }
        .f2{
            justify-content: space-between;
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            // border: solid 1px ;
            .col-6{
                padding: 10px;           
                p{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    color: white;
                    line-height: 24px;
                    @media (max-width:600px){
                        font-size: 14px;
                    }
                    @media (max-width:440px){
                        font-size: 12px;
                        line-height: 15px;
                    }
                    @media (max-width:370px){
                        font-size: 11px;                    
                    }
                }
                span{
                    font-style: normal;
                    font-weight: 1000;
                    font-size: 80px;
                    line-height: 94%;
                    font-weight: 700px;
                    color: white;
                    @media (max-width:600px){
                        font-size: 60px;
                    }
                    @media (max-width:440px){
                        font-size: 50px;
                        line-height: 70%;
                    }
                    @media (max-width:370px){
                        font-size: 40px;
                        line-height: 70%;
                    }
                }
            }
            @media (min-width:767.1px){
                display: none;
            }
        }
        .text{
            padding: 10px;
            margin-top: 30px;
            padding-right: 20px;
            @media (max-width:767px){
                display: none;
            }
            p{
                color: #021A38;
                opacity: 0.7;
                font-size: 18px;
                @media (max-width:1200px){
                    font-size: 16px;
                }
                @media (max-width:850px){
                    font-size: 14px;
                }
            }
            .btns{
                margin-top: 40px;
                .btn{
                    border: solid 1px red;
                    color: white;
                    background-color: red;
                    padding: 10px 40px;
                    border-radius: 8px;
                    &:hover{
                        background-color: white;
                        color: red;
                    }
                    @media (max-width:460px){
                        font-size: 14px;
                        padding: 10px 20px;
                    }
                    @media (max-width:390px){
                        font-size: 12px;
                        padding: 10px 20px;
                    }
                }                
            }
        }
        .world{
            position: absolute;
            right: 20px;
            top: 440px;
            height: 550px;
            // border: solid 1px gold;
            background: url('../images/world.png') no-repeat;
            background-size: 100% 100%;
            img{
                position: absolute;
                height: 100%;
            }
            @media (max-width:767px){
                display: none; 
            } 
            canvas{
                border: solid 1px orangered;
            }
        }
        .wh-count{
            margin-top: 250px;   
            @media (max-width:767px){
                margin-top: 100px;  
            }         
            h1{
                color: #021A38;
                text-align: left;
                font-weight: 700;
                font-size: 67.1213px;
                @media (max-width:1200px){
                    font-size: 60px;
                }
                @media (max-width:1100px){
                    font-size: 50px;
                }
                @media (max-width:850px){
                    font-size: 40px;
                }
                @media (max-width:767px){
                    color: white;
                    font-size: 50px;
                }
                @media (max-width:1629px){
                    font-size: 62px;
                }
                @media (max-width:1150px){
                    font-size: 50px;
                }
                @media (max-width:1000px){
                    font-size: 45px;
                }
                @media (max-width:850px){
                    font-size: 40px;
                }
                @media (max-width:700px){
                    font-size: 30px;
                } 
                @media (max-width:380px){
                    font-size: 25px;
                }          
            }
            p{
                color: #021A38;
                opacity: 0.7;
                font-size: 18px;
                @media (max-width:767px){
                    color: white;
                    font-size: 16px;
                }
                @media (max-width:390px){                  
                    font-size: 14px;
                } 
                @media (max-width:1200px){
                    font-size: 16px;
                }
                @media (max-width:850px){
                    font-size: 14px;
                }
            }
            .btns{
                margin-top: 40px;
                .btn{
                    border: solid 1px red;
                    color: white;
                    background-color: red;
                    padding: 10px 20px;
                    border-radius: 8px;
                    &:hover{
                        background-color: white;
                        color: red;
                    }
                    @media (max-width:460px){
                        font-size: 14px;
                        padding: 10px;
                    }
                    @media (max-width:390px){
                        font-size: 12px;
                        padding: 10px;
                    }
                }
                .media{
                    background-color: white;
                    margin-left: 30px;
                    color: red;
                    &:hover{
                        background-color:red;
                        color: white;
                    }
                    @media (max-width:390px){
                        margin-left: 10px;
                    }
                }
            }
        }
        .partners{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 250px;
            @media (max-width:767px){
                display: none;
            }
            .our{
                padding: 10px;
                h1{
                    color: white;
                    font-size: 70px;
                    text-align: left;
                    font-weight: 700;
                    -webkit-text-stroke: 1px #021A38;
                    font-family: arial;
                    @media (max-width:1350px){
                        font-size: 60px;
                    }
                    @media (max-width:1050px){
                        font-size: 55px;
                    }
                    @media (max-width:900px){
                        font-size: 45px;
                    }
                }
            }
            .imgs{
                display: flex;
                align-items: flex-start;
                justify-content: center;
                flex-wrap: wrap;
                .col-md-4{
                    padding: 40px;
                    @media (max-width:1050px){
                        padding: 20px;
                    }
                    img{
                        width: 100%;
                        @media (max-width:1200px){
                            width: 80%;
                        }
                    }
                }
            }
        }
    }
}