.Header{
    position: relative;
    padding: 0;
    margin: 0;
    height: 100%;
    width: 100%;
    // background: url('http://93.189.40.27:4300/files/home.png') no-repeat;
    background-size: 100% 100%;
    overflow: hidden;
    @media (max-width:767px){
        height: 120%;
    }
    @media (max-width:500px){
        height: 110%;
    }
    @media (max-width:450px){
        height: 100%;
    }
    .home-image{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        img{
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
    .wrapper{
        padding: 180px 15vw;
        @media (max-width:1629px){
            padding: 200px 15vw;
        }
        @media (max-width:1555px){
            padding:200px 15vw;
        }
        @media (max-width:1250px){
            padding: 200px 15vw;
        }
        @media (max-width:1000px){
            padding: 200px 15vw;
        }
        @media (max-width:870px){
            padding: 200px 15vw;
        }
        @media (max-width:767px){
            padding: 150px 150px;
        }
        @media (max-width:700px){
            padding: 150px 100px;
        }
        @media (max-width:560px){
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 150px 80px;
        }
        .row{
            .col-md-12{
                padding-right: 43vw;
                @media (max-width:1555px){
                    padding-right: 38vw;
                }
                @media (max-width:1485px){
                    padding-right: 36vw;
                }
                @media (max-width:1200px){
                    padding-right: 32vw;
                }
                @media (max-width:1070px){
                    padding-right: 25vw;
                }
                @media (max-width:850px){
                    padding-right: 18vw;
                }
                @media (max-width:767px){
                    padding-right: 0;
                }
                h1{
                    color: white;
                    font-family: 'Helvetica';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 70.1679px;
                    @media (max-width:1400px){
                        font-size: 60px;
                    }
                    @media (max-width:560px){
                        font-size: 50px;
                    }
                    @media (max-width:450px){
                        font-size: 45px;
                    }
                    @media (max-width:370px){
                        font-size: 40px;
                    }
                }
                p{
                    margin-top: 20px;
                    color: #FFFFFF;
                    opacity: 0.7;
                    font-family: 'Helvetica';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 18px;
                    @media (max-width:450px){
                        font-size: 16px;
                    }
                    @media (max-width:370px){
                        font-size: 14px;
                    }
                }
                .d-flex{
                    margin-top: 20px;
                    justify-content: start;
                    align-items: center;
                    .btn{
                        color: white;
                        border: solid 1px white;
                        margin-left: 20px;
                        padding: 10px 50px;
                        @media (max-width:450px){
                            font-size: 14px;
                            padding: 10px 40px;
                        }
                        @media (max-width:370px){
                            font-size: 13px;
                            padding: 10px 30px;
                        }
                        &:hover{
                            background-color: red;
                            border: solid 1px red;
                        }
                    }
                    .red{
                        background-color: red;
                        border: solid 1px red;
                        margin-left: 0;
                        &:hover{
                            background-color: transparent;
                            border: solid 1px white;
                        }
                    }
                }
            }
        }
    }
    .partners{
        padding: 10px 15vw 30px 15vw;
        .body{
            padding: 30px 20px;
            background-color: white;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .col-md-2{
                padding: 0 50px;
                img{
                    width: 100%;
                }
                @media (max-width:1550px){
                    padding: 0 20px;
                }
                @media (max-width:800px){
                    padding: 0 10px;
                }
                @media (max-width:400px){
                    padding: 0 5px;
                }
            }
            @media (max-width:1100px){
                padding: 20px;
            }
            @media (max-width:600px){
                padding: 10px;
            }
            @media (max-width:430px){
                padding: 5px;
            }
        }
        @media (max-width:1100px){
            padding: 30px 10vw;
        }
        @media (max-width:800px){
            padding: 30px 7vw;
        }
        @media (max-width:500px){
            padding: 0;
        }
    }
}