.About{
    position: relative;
    padding: 0;
    margin: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    @media (max-width:767px){
        background-color: #021A38;
    }
    .wrapper{
        padding: 200px 15vw;
        @media (max-width:1629px){
            padding: 200px 15vw;
        }
        @media (max-width:1555px){
            padding:200px 10vw;
        }
        @media (max-width:1250px){
            padding: 200px 10vw;
        }
        @media (max-width:1000px){
            padding: 200px 10vw;
        }
        @media (max-width:870px){
            padding: 200px 10vw;
        }
        @media (max-width:767px){
            padding: 150px 100px;
        }
        @media (max-width:700px){
            padding: 150px 80px;
        }
        @media (max-width:560px){            
            padding: 100px 50px;
            padding-top: 130px;
        }
        @media (max-width:320px){            
            padding: 150px 30px;
        }
        h1{
            text-align: center;
            font-size: 44px;
            font-family: "Helvetica";
            font-weight: 700;
            @media (max-width:767px){
                color: white;
            }
        }
        .img{
            margin-top: 100px;
            @media (max-width:767px){
                margin-top: 50px;
            }
            .i-frame{
                width: 100%;
                height: 600px;
                @media (max-width:900px){
                    height: 400px;
                }
                @media (max-width:500px){
                    height: 300px;
                }
                @media (max-width:420px){
                    height: 250px;
                }
            }
            img{
                width: 100%;
            }
        }
        .f1{
            margin-top: 100px;
            justify-content: center;
            .col-md-3{
                padding: 10px;
                p{
                    font-style: normal;
                    font-weight: 700;
                    font-size: 16px;
                    color: #021A38;
                    line-height: 24px;
                    @media (max-width:1050px){
                        font-size:14px;
                        font-weight: 600;
                    }
                }
                span{
                    font-style: normal;
                    font-weight: 1000;
                    font-size: 80px;
                    line-height: 94%;
                    font-weight: 700px;
                    color: #021A38;
                    @media (max-width:1050px){
                        font-size: 60px;
                    }
                    @media (max-width:800px){
                        font-size: 50px;
                    }
                }
            }
            @media (max-width:767px){
                display: none;
            }
        }
        .f2{
            margin-top: 40px;
            justify-content: space-between;
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            // border: solid 1px ;
            .col-6{
                padding: 10px;           
                p{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    color: white;
                    line-height: 24px;
                    @media (max-width:600px){
                        font-size: 14px;
                    }
                    @media (max-width:440px){
                        font-size: 12px;
                        line-height: 15px;
                    }
                    @media (max-width:370px){
                        font-size: 11px;                    
                    }
                }
                span{
                    font-style: normal;
                    font-weight: 1000;
                    font-size: 80px;
                    line-height: 94%;
                    font-weight: 700px;
                    color: white;
                    @media (max-width:600px){
                        font-size: 60px;
                    }
                    @media (max-width:440px){
                        font-size: 50px;
                        line-height: 70%;
                    }
                    @media (max-width:370px){
                        font-size: 40px;
                        line-height: 70%;
                    }
                }
            }
            @media (min-width:767.1px){
                display: none;
            }
        }
        .text{
            padding: 10px;
            margin-top: 70px;
            @media (max-width:767px){
                display: none;
            }
            p{
                color: #6B7280;
                opacity: 0.7;
            }
            .btns{
                margin-top: 40px;
                .btn{
                    border: solid 1px #021A38;
                    color: white;
                    background-color: #021A38;
                    padding: 10px 20px;
                    border-radius: 8px;
                    &:hover{
                        background-color: white;
                        color: #021A38;
                    }
                    @media (max-width:460px){
                        font-size: 14px;
                        padding: 10px;
                    }
                    @media (max-width:390px){
                        font-size: 12px;
                        padding: 10px;
                    }
                }
                .media{
                    background-color: white;
                    margin-left: 30px;
                    color: #021A38;
                    &:hover{
                        background-color:#021A38;
                        color: white;
                    }
                    @media (max-width:390px){
                        margin-left: 10px;
                    }
                }
            }
        }
        .partners{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 150px;
            @media (max-width:767px){
                display: none;
            }
            .our{
                padding: 10px;
                h1{
                    color: #021A38;
                    font-size: 65px;
                    text-align: left;
                    font-weight: 700;
                    @media (max-width:1450px){
                        font-size: 60px;
                    }
                    @media (max-width:1350px){
                        font-size: 55px;
                    }
                    @media (max-width:1050px){
                        font-size: 45px;
                    }
                    @media (max-width:900px){
                        font-size: 40px;
                    }
                }
            }
            .imgs{
                display: flex;
                align-items: flex-start;
                justify-content: center;
                flex-wrap: wrap;
                .col-md-4{
                    padding: 30px 50px;
                    @media (max-width:1250px){
                        padding: 30px 0 30px 100px;
                    }
                    @media (max-width:1050px){
                        padding: 20px 0 20px 70px;
                    }
                    @media (max-width:900px){
                        padding: 10px 0 10px 70px;
                    }
                    img{
                        width: 100%;
                        @media (max-width:1500px){
                            width: 80%;
                        }
                    }
                }
            }
        }
        .wh-count{
            margin-top: 70px;
            @media (min-width:767.1px){
                // display: none;                
            }
            h1{
                color: white;
                text-align: left;
                font-size: 35px;
                @media (min-width:767.1px){
                    color: #021A38;
                }
            }
            p{
                color: #FFFFFF;
                opacity: 0.7;
                font-size: 12px;
                @media (min-width:767.1px){
                    font-size: 16px;
                    color: #6B7280;
                }
            }
            .btns{
                margin-top: 40px;
                .btn{
                    border: solid 1px red;
                    color: white;
                    background-color: red;
                    padding: 10px 20px;
                    border-radius: 8px;
                    &:hover{
                        background-color: white;
                        color: red;
                    }
                    @media (max-width:460px){
                        font-size: 14px;
                        padding: 10px;
                    }
                    @media (max-width:390px){
                        font-size: 12px;
                        padding: 10px;
                    }
                }
                .media{
                    background-color: white;
                    margin-left: 30px;
                    color: red;
                    &:hover{
                        background-color:red;
                        color: white;
                    }
                    @media (max-width:390px){
                        margin-left: 10px;
                    }
                }
            }
        }
        .wh{
            margin-top: 100px;
            @media (min-width:767.1px){
                display: none;
            }
            p{
                color: #FFFFFF;
                opacity: 0.7;
                font-size: 12px;
            }
            .btns{
                margin-top: 40px;
                .btn{
                    border: solid 1px red;
                    color: white;
                    background-color: red;
                    padding: 10px 20px;
                    border-radius: 8px;
                    &:hover{
                        background-color: white;
                        color: red;
                    }
                    @media (max-width:460px){
                        font-size: 14px;
                        padding: 10px;
                    }
                    @media (max-width:390px){
                        font-size: 12px;
                        padding: 10px;
                    }
                }
                .media{
                    background-color: white;
                    margin-left: 30px;
                    color: red;
                    &:hover{
                        background-color:red;
                        color: white;
                    }
                    @media (max-width:390px){
                        margin-left: 10px;
                    }
                }
            }
        }
    }
}